.")])]),_c('p',[_c('span',{staticClass:"font-weight-bold"},[_vm._v("Abstract:")]),_vm._v("This paper proposes a sound event detection system that is trained using a minimally annotated data set of single sounds to identify and separate components of polyphonic sounds. The system uses a Feed Forward Neural Network with a single hidden layer that is pre-trained using an autoencoder. Single sounds, represented as Mel Frequency Cepstral Coefficient (MFCC) feature vectors, are used to train the neural network using back propagation. Polyphonic sounds are pre-processed using Principal Component Analysis (PCA) and Nonnegative Matrix Factorization (NMF) to obtain source separated sounds. These source separated sounds are then tested for sound classification using the feed-forward algorithm. Our system is able to achieve reasonable accuracy of source separation and detection with minimal training set. The ultimate goal of our system is to bootstrap from minimal data and learn new sounds leading to a better sound detection system.")]),_c('hr',{staticClass:"py-2"})]),_c('div',{staticClass:"py-3 mb-5"},[_c('a',{attrs:{"href":"https://ieeexplore.ieee.org/document/8074107"}},[_c('h4',[_vm._v("TargetZIKA: Epidemic Situation Detection and Risk Preparedness for ZIKA Virus (2017 The 10th International Conference on Ubi-media Computing, Ubi-media held at Siam bayshore Pattaya, Thailand.).")])]),_c('p',[_c('span',{staticClass:"font-weight-bold"},[_vm._v("Abstract:")]),_vm._v("We propose a framework for disaster situation modeling in smart city using micro-reporting from the most efficient and reliable sources of multimedia, people with mobile phones. By using micro-reports generated from a crowdsourced mobile phone application and EventShop to analyze all kinds of Zika related data, TargetZIKA aims to combat the spread of the Zika virus in Brazil. EventShop is an open-source platform which provides a generic infrastructure for ingesting and aggregating heterogeneous spatio-temporal dat